import { NavLink } from "react-router-dom";
import s from "./NavigationMain.module.css";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import Profile from "../../pages/Profile/ProfileMain";
import { useState } from "react";
import hamburger from "../../assets/images/hamburger.png";

const NavigationMain = () => {
  const user = JSON.parse(window.localStorage.getItem("user"));
  const { isAuthorized } = useSelector((state) => state.authMe.authorize);

  const [click, setClick] = useState(Boolean);
  const handleClick = () => setClick(!click);
  const btnClick = () => {
    if (window.innerWidth < 900) {
      return handleClick();
    }
  };

  const button = (route, value, style = null, authAction = null) => {
    return (
      <button type={"submit"} className={style} onClick={authAction}>
        <NavLink to={route}>{value}</NavLink>
      </button>
    );
  };

  const getWindowSize = () => {
    const { innerWidth } = window;
    return { innerWidth };
  };
  const [windowSize, setWindowSize] = useState(getWindowSize());

  useEffect(() => {
    function handleWindowResize() {
      setWindowSize(getWindowSize());
    }
    window.addEventListener("resize", handleWindowResize);
  }, []);
  const dynamicStyle =
    !click || windowSize.innerWidth > 900 ? s.menuMain : s.show;
  const logOutBtnStyle = windowSize.innerWidth < 900 ? s.item : s.logout;
  return (
    <div>
      <nav>
        <button className={s.burger} onClick={handleClick}>
          <img src={hamburger} alt="humburger" />
        </button>
        <div className={dynamicStyle}>
          {button("posts", "Article", s.item, btnClick)}
          {button("announcements", "Announcements", s.item, btnClick)}
          {button("user", "User", s.item, btnClick)}
          {isAuthorized ? (
            <>
              <Profile user={user} />
              {button("logout", "Log out", logOutBtnStyle, btnClick)}
            </>
          ) : (
            <div className={s.authMe}>
              {button("auth", "Log In", s.item, btnClick)}
              {button("register", "Register", s.item, btnClick)}
            </div>
          )}
        </div>
        {isAuthorized && <Profile user={user} />}
      </nav>
    </div>
  );
};

export default NavigationMain;
